 *
 * SUMMARY:
 * 1) RESET FILES
 * 2) INPUT
 * 3) WP CORE
 */



/* **************************************** *
 * RESET FILES
 * **************************************** */
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
time,
figure,
article,
nav,
header,
footer,
hgroup,
video,
audio,
aside,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
font,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
    margin: 0;
    padding: 0;
    border: 0;
    font-weight: inherit;
    font-style: inherit;
    vertical-align: baseline;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

ol,
ul {
    list-style: none;
}

table {
    border-collapse: separate;
    border-spacing: 0;
}

caption,
th,
td {
    text-align: left;
    font-weight: normal;
}

figure,
article,
header,
footer,
nav,
hgroup,
video,
audio,
aside,
main {
    display: block;
}

/* reset mobile default style */
html {
    -ms-text-size-adjust: none;
    -webkit-text-size-adjust: none;
    -moz-text-size-adjust: none;
    text-size-adjust: none;
}

body,
textarea:focus,
input:focus,
a:focus {
    -webkit-tap-highlight-color: rgba(50, 65, 140, .08);
}

body {
    -webkit-touch-callout: none;
}

/* selection */
::-moz-selection {
    background: rgba(255, 238, 51, 0.99);
    color: #191919;
}

::selection {
    background: rgba(255, 238, 51, 0.99);
    color: #191919;
}

img::-moz-selection {
    background: none;
}

img::selection {
    background: none;
}



/* **************************************** *
 * INPUT
 * **************************************** */
input[type=text],
input[type=tel],
input[type=email],
input[type=password],
input[type=url],
input[type=number],
input[type=search],
textarea {
    -webkit-appearance: none;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    outline: none;
    font-family: sans-serif;
}

input[type=submit],
input[type=button],
button {
    -webkit-appearance: none;
    font-family: 'KakaoBig', sans-serif;
    border-radius: 0rem;
}

input[type=radio] {
    border-radius: 100%;
}

/* PLACEHOLDER */
::-webkit-input-placeholder {
    font-family: 'KakaoBig', sans-serif;
    font-size: 18rem;
    color: #aaa;
}

::-moz-placeholder {
    font-family: 'KakaoBig', sans-serif;
    font-size: 18rem;
    color: #aaa;
}

:-ms-input-placeholder {
    font-family: 'KakaoBig', sans-serif;
    font-size: 18rem;
    color: #aaa;
}

input:focus::-webkit-input-placeholder,
textarea:focus::-webkit-input-placeholder {
    color: transparent;
}

input:focus::-moz-placeholder,
textarea:focus::-moz-placeholder {
    color: transparent;
}

input:focus:-ms-input-placeholder,
textarea:focus:-ms-input-placeholder {
    color: transparent;
}

/* reset some shadow dom */
/* clears the 'X' from Internet Explorer Chrome */
input::-ms-clear {
    display: none;
    width: 0;
    height: 0;
}

input::-ms-reveal {
    display: none;
    width: 0;
    height: 0;
}

input::-webkit-search-decoration,
input::-webkit-search-cancel-button,
input::-webkit-search-results-button,
input::-webkit-search-results-decoration {
    display: none;
}



/* **************************************** *
 * WP CORE
 * **************************************** */
ul#wp-admin-bar-root-default {
    float: left;
}

.screen-reader-text {
    text-indent: -9999rem;
    display: inline-block;
    overflow: hidden;
}




